// THEME: Aaron 4 Arizona
// #/



//// Import CSS for fonts
@import url( 'https://use.typekit.net/wqf7erp.css' );





//// Colors
$primary: #F47E3A;
$primaryLight: #F47E3A;
$primaryDark: #173D6E;

// $secondary: #485781;
// $secondaryLight:
// $secondaryDark:

$white: white;
$black: $primaryDark;

$gray: #c2c2c2;
$grayLight: #c2c2c2;





//// Fonts - Headline & Copy Styles
$fontHeadline: 'Poppins', sans-serif;
$fontHeadlineWeight: 800;
$fontHeadlineColor: $primary;

$fontCopy: $fontHeadline;
$fontCopyWeight: 500;
$fontCopyBold: 700;
$fontCopyColor: $black;

$text: $fontHeadline;
$textWeight: $fontCopyWeight;
$textBold: $fontCopyBold;
$textColor:	$black;
$textLink: $primary;



h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $fontHeadline !important;
	font-weight: $fontHeadlineWeight !important;

	color: $fontHeadlineColor !important;
}

p {
	font-family: $fontCopy !important;
	font-weight: $fontCopyWeight !important;

	color: $fontCopyColor !important;
}

strong {
	font-weight: $fontCopyBold !important;
}

.fa-lead {
	font-family: $text !important;
	font-weight: $textWeight !important;

	color: $textColor !important;



	a {
		font-weight: $textBold !important;

		color: $textLink !important;
	}
}

.fastAction .circle {
	&:hover,
	&:active,
	&:focus {
		background: $textLink !important;
	}
}





//// Legend & Label Styles
$legend: $fontHeadline;
$legendWeight: $fontHeadlineWeight;
$legendColor: $primaryLight;

$label: $fontHeadline;
$labelWeight: $fontHeadlineWeight;
$labelLight: $fontCopyWeight;
$labelColor: $primary;

$option: $fontHeadline;
$optionWeight: $fontCopyWeight;
$optionColor: $black;



legend {
	font-family: $legend !important;
	font-weight: $legendWeight !important;

	color: $legendColor !important;
}

label {
	font-family: $label !important;
	font-weight: $labelWeight !important;

	color: $labelColor !important;



	&.at-check,
	.radios & {
		font-family: $option !important;
		font-weight: $optionWeight !important;

		color: $optionColor !important;
	}

	input[type='checkbox'] + span {
		font-family: $option !important;
		font-weight: $optionWeight !important;

		color: $optionColor !important;
	}
}

.updateMyProfileSection .text {
	font-weight: $labelLight !important;
}





//// Form Styles
$outline: $gray;
$outlineWeight: 1px;
$outlineHover: $primary;
$outlineRadius: 0;

$input: $fontHeadline;
$inputWeight: $fontCopyWeight;
$inputColor: $primary;



.ngp-form {
	input,
	select,
	textarea,
	.allow-dropdown,
	.at-checkbox-title-container:before {
		border-color: $outline !important;
		border-width: $outlineWeight !important;
		border-radius: $outlineRadius !important;
	}

	input,
	select,
	textarea,
	.allow-dropdown {
		&:hover {
			border-color: $outlineHover !important;
		}
	}

	.at-checkbox-title-container:after {
		color: $outlineHover !important;
	}

	.at-checkbox-title-container:hover {
		&:before {
			border-color: $outlineHover !important;
		}
	}

	input[type=radio]:before {
		background: $outlineHover !important;
	}
}

input,
select,
textarea {
	font-family: $input !important;
	font-weight: $inputWeight !important;

	color: $inputColor !important;
}

.updateMyProfileSection label {
	input[type="checkbox"] + span {
		&:before {
			border-color: $outline !important;
			border-width: $outlineWeight !important;
			border-radius: $outlineRadius !important;
		}

		&:after {
			color: $outlineHover !important;
		}
	}

	&:hover {
		input[type="checkbox"] + span {
			&:before {
				border-color: $outlineHover !important;
			}
		}
	}
}





//// Button Styles
$button: $fontHeadline;
$buttonWeight: $fontHeadlineWeight;
$buttonColor: $white;
$buttonCase: uppercase;
$buttonSize: 1.7em;

$buttonBkg: $primary;
$buttonHover: $primaryLight;

$buttonBorder: none;
$buttonRadius: 50;

$buttonTransition: background 0.50s ease-in-out;



input.at-submit,
input.btn-at-primary {
	font-family: $button !important;
	font-weight: $buttonWeight !important;
	font-size: $buttonSize !important;

	color: $buttonColor !important;

	text-transform: $buttonCase !important;

	background: $buttonBkg !important;

	border: $buttonBorder !important;
	border-radius: $buttonRadius !important;

	transition: $buttonTransition !important;



	&:hover,
	&:active,
	&:focus {
		background: $buttonHover !important;
	}
}




//// Footer Styles
$footer: $fontHeadline;
$footerWeight: $fontHeadlineWeight;
$footerColor: $primary;

$footerBorder: $primary;
$footerBorderSize: 2px;

$footerBkg: $white;



.footer__links a,
.footer__message {
	font-family: $footer !important;
	font-weight: $footerWeight !important;

	color: $footerColor !important;
}

.footer__message {
	border-color: $footerBorder !important;
	border-width: $footerBorderSize !important;

	a {
		color: $footerColor !important;
	}
}

footer {
	background: $footerBkg !important;
}





//// Backgrounds & Gradients
$html: $primary;

$container: $white;

$background: $primaryLight;
$backgroundGradientStart: $primary;
$backgroundGradientEnd: $primaryLight;



html {
	background: $html !important;
}

article {
	background: $container !important;
}

main {
	background-color: $background !important;

	&.color,
	&.Color {
		background-color: $background !important;
	}
	
	&.gradient,
	&.Gradient {
		background-image: linear-gradient(
				to bottom right,
				$backgroundGradientStart,
				$backgroundGradientEnd
			) !important;
	}
}